* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.login_page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
}

.login_disp {
    display: flex;
    align-items: center;
    background-color: white;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    overflow: hidden;
}

.home_image {
    flex: 1;
    padding: 20px;
    text-align: center;
}

.att_image {
    width: 80%;
    max-width: 300px;
    height: auto;
}

.home_login {
    flex: 1;
    padding: 20px;
}

.login_email,
.login_pass {
    margin-bottom: 15px;
}

.email,
.password {
    width: 100%;
    padding: 10px;
    border: none;
    border-bottom: 2px solid #ccc;
    transition: border-color 0.3s;
}

.email:focus,
.password:focus {
    border-color: #29b6f6;
    outline: none;
}

.clicked {
    border-color: #29b6f6 !important;
}

.press {
    text-align: right;
    margin-top: 10px;
}

.itsLink {
    text-decoration: none;
    color: #29b6f6;
    transition: color 0.3s;
}

.itsLink:hover {
    color: #047edf;
}

.message {
    margin-top: 10px;
    padding: 8px;
    text-align: center;
    font-weight: bold;
}

@media screen and (max-width: 768px) {
    .login_disp {
        flex-direction: column;
    }

    .home_image {
        padding: 0;
    }

    .att_image {
        margin-top: 20px;
    }
}